<template>
  <div>
    <div class="border rounded" style="height:80px">
      <div class="font-weight-bold pt-3">Средняя скорость</div>
      <div style="font-size:20px; color:red">{{ averSpeed }} км/ч</div>
    </div>
  </div>
</template>

<script>
import db from "./firebase-init.js";
import { todayDates, yesterdayDates } from "../getDates";

export default {
  name: "averageSpeed",
  props: {
    type: String
  },
  data() {
    return {
      averSpeed: 0
    };
  },
  created: function() {
    if (this.type == "today") {
      let date = todayDates.pathDay;
      let flightsObj = db.ref(date);

      flightsObj.once("value").then(Snapshot => {
        const res = Snapshot.val();
        this.averSpeed = Math.round(
          Object.values(res).reduce((acc, c) => acc + (c.velocity || 0), 0) /
            Object.values(res).length
        );
      });
    }

    if (this.type == "yesterday") {
      let date = yesterdayDates.pathDay;
      let flightsObj = db.ref(date);

      flightsObj.once("value").then(Snapshot => {
        const res = Snapshot.val();
        this.averSpeed = Math.round(
          Object.values(res).reduce((acc, c) => acc + (c.velocity || 0), 0) /
            Object.values(res).length
        );
      });
    }

    if (this.type == "month") {
      let date = todayDates.pathMonth;
      let flightsObj = db.ref(date);

      flightsObj.once("value").then(Snapshot => {
        const res = Snapshot.val();

        let sum = 0;
        let average = Object.values(res).length;

        Object.values(res).forEach(function(item) {
          sum =
            sum +
            Math.round(
              Object.values(item).reduce(
                (acc, c) => acc + (c.velocity || 0),
                0
              ) / Object.values(item).length
            );
        });
        this.averSpeed = Math.round(sum / average);
      });
    }

    if (this.type == "year") {
      let date = todayDates.pathYear;

      let flightsObj = db.ref(date);

      flightsObj.once("value").then(Snapshot => {
        const res = Snapshot.val();

        let sum = 0;
        let average;

        Object.values(res).forEach(function(item) {
          Object.values(item).forEach(function(resItem) {
            average = Object.values(item).length;
            sum =
              sum +
              Math.round(
                Object.values(resItem).reduce(
                  (acc, c) => acc + (c.velocity || 0),
                  0
                ) / Object.values(resItem).length
              );
          });
        });
        this.averSpeed = Math.round(sum / average);
      });
    }
  }
};
</script>

