          Object.values(item).forEach(function(resItem) {
            sumRes.push(resItem);
          });
        });

        Object.assign(res, sumRes);

        let sortedArr = Object.values(res).filter(item => {
          return item.time > "07:00:00" && item.time < "12:59:00";
        });

        this.time2 = sortedArr.length;
      });

      flightsObj.once("value").then(Snapshot => {
        let res = Snapshot.val();
        //alert(typeof res);

