        let sumRes = [];

        Object.values(res).forEach(function(item) {
          Object.values(item).forEach(function(resItem) {
            Object.values(resItem).forEach(function(done) {
              sumRes.push(done);
            });
          });
        });

        Object.assign(res, sumRes);

        let sortedArr = Object.values(res).filter(item => {
