/* eslint-disable no-console */

let today = new Date();
today.setHours(today.getHours() + 3);

let yesterday = new Date();
yesterday.setDate(yesterday.getDate() - 1);
yesterday.setHours(yesterday.getHours() + 3);

function getDates(date) {
    return {
        today: today,
        year: date.getFullYear(),
        month: (date.getMonth() + 1),
        day: date.getDate(),
        pathDay: date.getFullYear() + '/' + (date.getMonth() + 1) + '/' + date.getDate() + '-' + (date.getMonth() + 1) + '-' + date.getFullYear(),
        pathDate: date.getFullYear() + '-' + (date.getMonth() + 1) + '-' + date.getDate(),
        pathMonth: date.getFullYear() + '/' + (date.getMonth() + 1),
        pathYear: date.getFullYear()
    }
}

const todayDates = getDates(today);
const yesterdayDates = getDates(yesterday);

// console.log(today);

export {
    todayDates,
    yesterdayDates
};